import axios from 'axios'
import Joi from 'joi'
import React, { useContext, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { AuthContext } from '../../Context/AuthContext'
import bg from '../../images/bg.jpg'
import styles from './Login.module.scss'

export default function Login() {
 let {saveUserData}=useContext(AuthContext)

  const [user, setUser] = useState({
    email: "",
    password: ""
    
  })

  const [errorMsg, setErrorMsg] = useState("")

  const [errorlist, setErrorList] = useState([])

  let navigate=useNavigate();

  let getInputValue=(e)=>{
    let myUser = { ...user }
    myUser[e.target.name]=e.target.value
    setUser(myUser)
    
   
  }

  let validateRegisteration=()=>{
    let schema= Joi.object({
     email:Joi.string().required().email({tlds:{allow:['com','net']}}),
     password:Joi.string().required().pattern(/^[a-z]\w{7,20}$/)
     })
   return  schema.validate(user,{abortEarly:false})
   }


   let submitFormData= async(e)=>{
    e.preventDefault();

    let validationResponse=validateRegisteration();
    if(validationResponse.error)
    { 
      setErrorList(validationResponse.error.details)
    }
    else
    {
      let {data}= await axios.post('https://route-movies-api.vercel.app/signin',user)
      console.log(data); 

      if(data.message=='success') 
      {
        localStorage.setItem("token",data.token)
        saveUserData();
        navigate('/')
      }
      else{
      setErrorMsg(data.message)

      }
    }

   
  }


  return (
    <>

 <div className={`${styles.bg}  `}>

  <div className={`w-50  rounded-2  mx-auto mt-5 py-5 px-3 ${styles.bgLogin} `}>
      <h2 className=''>Login</h2>
      
    {errorMsg? <div className='alert alert-danger text-center p-1'>{errorMsg}</div>:""}

      <form onSubmit={submitFormData} className='my-3'>

        <div className='my-2'>
          <label  htmlFor='email'>email</label>
          <input onChange={getInputValue} type="email"  className='form-control my-2' name='email' />
          {errorlist.map((error,index)=>error.context.label=="email"?<div key={index} className='alert alert-danger p-0'>{error.message}</div>:"")}
        </div>
        <div className='my-2'>
          <label  htmlFor='password'>Password</label>
          <input onChange={getInputValue} type="password" className='form-control my-2'name='password' />
          {errorlist.map((error,index)=>error.context.label=="password"?<div key={index} className='alert alert-danger p-0'>password must be at least 8 characters and starts with a letter</div>:"")}
        </div>
       <div className="text-center">
       <button className='btn btn-danger px-5 my-3 '>Login</button>
       </div>
      </form>
    </div>
    </div>
    </>
  )
}
