import React, { useContext } from "react";
import { Link, NavLink } from "react-router-dom";
import { AuthContext } from "../../Context/AuthContext";
import styles from './Navbar.module.scss';



export default function Navbar() {
  let {userData,logout} = useContext(AuthContext)
  return (
    <nav className={`navbar navbar-expand-lg fixed-top  ${styles.bgNav} ` }>
    <div className="container-fluid">
      <Link to={""} className="navbar-brand text-danger fw-bold fs-3" >Noxe</Link>
      <button className="navbar-toggler bg-white text-secondary" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon "></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        {userData?<ul className="navbar-nav me-auto mb-2 mb-lg-0">
          <li className="nav-item">
            <NavLink className={({ isActive }) => (isActive ? ' nav-link text-danger  fw-bold ' : ' active nav-link ')} to={''}>Home</NavLink>
          </li>
          <li className="nav-item">
            <NavLink className={({ isActive }) => (isActive ? ' nav-link text-danger  fw-bold ' : ' active nav-link ')} to={"movies"}>Movies</NavLink>
          </li>
          <li className="nav-item">
            <NavLink className={({ isActive }) => (isActive ? ' nav-link text-danger  fw-bold ' : ' active nav-link ')} to={'tvshows'}>Tv Shows</NavLink>
          </li>
          <li className="nav-item">
            <NavLink className={({ isActive }) => (isActive ? ' nav-link text-danger  fw-bold ' : ' active nav-link ')} to={'people'}>People</NavLink>
          </li>
          
      
         
          </ul>:''}
        

          <ul className="navbar-nav ms-auto mb-2 mb-lg-0 ">
          <li className="d-flex align-items-center mb-2 mb-lg-0 me-4">
            <i className="fab fa-facebook "></i>
            <i className="fab fa-spotify ms-3"></i>
            <i className="fab fa-instagram ms-3"></i>
            <i className="fab fa-youtube ms-3"></i>
          </li>
      
          {userData?<><li className="nav-item d-flex align-items-center justify-content-center">
            <Link className="nav-link" to={'profile'} title="profile"><i className="fa-solid fa-user text-danger fs-4  "></i></Link>
          </li>
          <li><Link className="nav-link btn btn-outline-danger "  onClick={logout}>Logout</Link></li></>
          :
          <><li className="nav-item mb-2 mb-lg-0">
            <Link className="nav-link bg-black btn btn-light" aria-current="page" to={'login'}>Login</Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link ms-lg-2  btn btn-danger " aria-current="page" to={'register'}>Register</Link>
          </li>
          </>}
       
  
          </ul>


     
     
      </div>
    </div>
  </nav>
  )
}
