import React from 'react'
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import axios from 'axios';
import { useEffect,useState } from 'react';
import { Link } from 'react-router-dom';
import styles from './Header.module.css'


export default function HomeHeader() {
    const [trendingMovies,setTrendingMovies]=useState([])
    useEffect(()=>{ 
        getTrendingMovies();
      } ,[])
    async function getTrendingMovies(){
    let {data}= await axios.get(`https://api.themoviedb.org/3/trending/movie/day?api_key=bdd10d2b8f52bc0a5320d5c9d88bd1ff`)
    setTrendingMovies(data.results);

    }
    
      const settings = {
        infinite: true,
        slidesToShow: 7,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 800
      };
  return (
    <>
  
    <div  className= {`${styles.header} d-flex align-items-end `} >
   <div className='container  mt-5 py-5 '>
  <div className='row  g-2'>
   <Slider {...settings}>
   {trendingMovies.slice(0,15).map((item , index)=>
    <Link key={index}  to={`/details/${item.media_type}/${item.id}`}>
      <div className='imgslider p-1 item '>
       <img src={'https://image.tmdb.org/t/p/w500'+item.poster_path} className='w-100 rounded' alt=""/>
      </div> 
    </Link>
     )}
      
    </Slider>
   </div>
   </div>
  
   </div>
 
    </>
  )
}

